import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Draggable from 'react-draggable';
import { Resizable } from 'react-resizable';
import './BookDesignPage.css';
import html2canvas from 'html2canvas';
import textIcon from '../assets/images/text.png';
import imageIcon from '../assets/images/image.png';
import back from '../assets/images/exit.png';

function BookDesignPage({ onClose, onComplete }) {
  const navigate = useNavigate();
  const bookCoverRef = useRef(null);
  const [addImageMenuVisible, setAddImageMenuVisible] = useState(false); //  
  const [ImgSubmenuPosition, setImgSubmenuPosition] = useState({ x: 0, y: 0 });
  const [addTextMenuVisible, setAddTextMenuVisible] = useState(false); //  
  const [TextSubmenuPosition, setTextSubmenuPosition] = useState({ x: 0, y: 0 });
  const fileInputRef = useRef(null);
  const [textElements, setTextElements] = useState([]);
  const [selectedTextId, setSelectedTextId] = useState(null);
  const [showPopup, setShowPopup] = useState(false); //  
  const [textStyle, setTextStyle] = useState({ fontSize: 16, color: '#000000' }); //  
  const [showColorPicker, setShowColorPicker] = useState(false); //     
  const [bookCoverColor, setBookCoverColor] = useState('#E3CAA5'); //    
  const [imageSrc, setImageSrc] = useState(null);
  const [imageSize, setImageSize] = useState({ width: 150, height: 150 });
  const [popupVisible, setPopupVisible] = useState(false);
  const [popupPosition, setPopupPosition] = useState({ x: 0, y: 0 });
  const [selectedImage, setSelectedImage] = useState(null);
  const [showDeletePopup, setShowDeletePopup] = useState(false);
  const [deletePopupPosition, setDeletePopupPosition] = useState({ x: 0, y: 0 });
  const [resizeColor, setResizeColor] = useState('#000');
  const imageRef = useRef(null);
  const [resizeEnabled, setResizeEnabled] = useState(false); //    

//     
const saveBookCoverAsImage = () => {
  const bookCover = bookCoverRef.current;

  //     html2canvas  
  const images = bookCover.querySelectorAll('img');
  const imagePromises = Array.from(images).map((img) => {
    return new Promise((resolve, reject) => {
      if (img.complete) {
        resolve();
      } else {
        img.onload = resolve;
        img.onerror = reject;
      }
    });
  });

  //      
  Promise.all(imagePromises)
    .then(() => {
      html2canvas(bookCover, {
        useCORS: true, //     CORS 
        allowTaint: false, //    
        logging: true, //    
      })
        .then((canvas) => {
          const imageData = canvas.toDataURL('image/png'); //   Base64 
          
          //   URL  Base64 
          onComplete(imageData);  //  URL onComplete  
        })
        .catch((error) => {
          console.error("Error capturing the book cover:", error);
        });
    })
    .catch((error) => {
      console.error("Error loading images before capture:", error);
    });
};

  //  /  
  const toggleColorPalette = () => {
    setShowColorPicker((prev) => !prev);
  };

  //   
  const handleColorChange = (event) => {
    setBookCoverColor(event.target.value);
  };

  //    
  const handleColorChange2 = (event) => {
    setTextStyle((prev) => ({ ...prev, color: event.target.value })); //   
  };

  //      
  const handleTextClick = () => {
    const newTextElement = {
      id: Date.now(),
      text: " ", //  
      x: 50, //   X
      y: 50, //   Y
      fontSize: 16, //   
    };

    setTextElements((prev) => [...prev, newTextElement]); //   
  };

  //   
  const handleTextChange = (id, event) => {
    const updatedText = event.target.innerText; //   
    setTextElements((prev) =>
      prev.map((textElement) =>
        textElement.id === id ? { ...textElement, text: updatedText } : textElement
      )
    );
  };
  //     
  const handleTextClickForEditing = (id) => {
    setSelectedTextId(id);
    const selectedText = textElements.find((textElement) => textElement.id === id);
    setTextStyle({ fontSize: selectedText.fontSize, color: selectedText.color });
    setShowPopup(true);
  };

  //    
  const handleFontSizeChange = (event) => {
    setTextStyle((prev) => ({ ...prev, fontSize: parseInt(event.target.value, 10) }));
  };

  //   
  const handleSaveStyle = () => {
    setTextElements((prev) =>
      prev.map((textElement) =>
        textElement.id === selectedTextId
          ? { ...textElement, fontSize: textStyle.fontSize, color: textStyle.color }
          : textElement
      )
    );
    setShowPopup(false); //  
  };

  //       
  const handleImageClick = () => {
    setAddImageMenuVisible(true); //  
    setImgSubmenuPosition({ x: 350, y: 150 }); //    ( )
  };

  //    (Back   )
  const handleImgBackClick = () => {
    setAddImageMenuVisible(false); //  
  };
  //    
  const handleImageUpload = (event) => {
    const file = event.target.files[0]; //    
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImageSrc(reader.result); // base64   
      };
      reader.readAsDataURL(file);
    }
  };
  //    
  const handleResize = (event, { size }) => {
    setImageSize(size);
    //          
    if (size.width !== imageSize.width) {
      setResizeColor('blue'); //    
    } else if (size.height !== imageSize.height) {
      setResizeColor('blue'); //    
    }
  };
 //  ,    
 const handleRightClick = (e) => {
  e.preventDefault();
  const rect = e.target.getBoundingClientRect();
  setDeletePopupPosition({
    x: rect.right + window.scrollX + 10,
    y: rect.bottom + window.scrollY + 10, 
  });
  setResizeEnabled(true);
  setShowDeletePopup(true);
};

//  
const handleDelete = () => {
  setImageSrc(null); //  
  setShowDeletePopup(false); //  
};

//     
const handleClickOutside = (e) => {
  if (imageRef.current && !imageRef.current.contains(e.target)) {
    setShowDeletePopup(false);
  }
};

//     ,   
useEffect(() => {
  document.addEventListener('click', handleClickOutside);
  return () => {
    document.removeEventListener('click', handleClickOutside);
  };
}, []);

  //    (Back   )
  const handleTextBackClick = () => {
    setAddTextMenuVisible(false); //  
  };
  return (
    <div className="book-design-page">
      <img src={back} alt="back" className="back-icon" onClick={onClose} />
      {/*   */}
      <div className={`design-tools`}>
        <div className="tool-button">
          <button className="color-button" onClick={toggleColorPalette} style={{ backgroundColor: bookCoverColor }}></button>
        </div>
        <button className="tool-button" onClick={handleTextClick}>
          <img src={textIcon} alt="Text" />
        </button>
        <button className="tool-button" onClick={handleImageClick}>
          <img src={imageIcon} alt="Image" />
        </button>
      </div>
      {/*    */}
        {showColorPicker && (
          <div className="color-palette">
            <label> </label>
            <input
              type="color"
              value={bookCoverColor}
              onChange={handleColorChange} //    
            />
          </div>
        )}
      {/*   */}
      {addTextMenuVisible && (
          <div className="add-popup"
            style={{
              position: 'absolute',
              top: `${TextSubmenuPosition.y}px`,
              left: `${TextSubmenuPosition.x}px`,
            }}>
            <button onClick={handleTextBackClick}>Back</button>
          </div>
        )}
        {/*    */}
        {showPopup && (
          <div className="text-options-popup"
          style={{
            position: 'absolute',
            top: `${TextSubmenuPosition.y+130}px`,
            left: `${TextSubmenuPosition.x+250}px`,
            }}>
            <h3> </h3>
            <div>
              <label>  </label>
              <input
                type="number"
                value={textStyle.fontSize}
                onChange={handleFontSizeChange}
                min="10"
                max="100"
              />
            </div>
            <div>
              <label>  </label>
              <input type="color" value={textStyle.color} onChange={handleColorChange2} />
            </div>
            <button onClick={handleSaveStyle}></button>
          </div>
        )}
      {/*   */}
      {addImageMenuVisible && (
          <div className="add-popup"
            style={{
              position: 'absolute',
              top: `${ImgSubmenuPosition.y}px`,
              left: `${ImgSubmenuPosition.x}px`,
            }}>
            <button>AI  </button>
            <button onClick={() => fileInputRef.current.click()}> </button>
            {/*    */}
            <input
              type="file"
              accept="image/*"
              ref={fileInputRef}
              style={{ display: 'none' }}
              onChange={handleImageUpload}
            />

            <button onClick={handleImgBackClick}>Back</button>
          </div>
        )}
        
        <div className = "book-container">
        <div className="book-cover"
              ref={bookCoverRef} 
              style={{ backgroundColor: bookCoverColor }}>
            {textElements.map((textElement) => (
              <Draggable
                key={textElement.id}
                defaultPosition={{ x: textElement.x, y: textElement.y }}
                bounds=".book-cover"
              >
                <div
                  contentEditable
                  suppressContentEditableWarning={true}
                  onBlur={(event) => handleTextChange(textElement.id, event)}
                  onClick={() => handleTextClickForEditing(textElement.id)}
                  style={{
                    position: 'absolute',
                    fontSize: `${textElement.fontSize}px`,
                    color: textElement.color,
                    padding: '5px',
                    border: '1px dashed #ccc',
                    cursor: 'move',
                  }}
                >
                  {textElement.text}
                </div>
              </Draggable>
            ))}
          </div>
          {/*   */}
          {imageSrc && (
            <Draggable bounds=".book-container">
              <Resizable
                width={imageSize.width}
                height={imageSize.height}
                onResize={(event, { size }) => handleResize(size)}
                minConstraints={[100, 100]}
                maxConstraints={[400, 400]}
                resizeHandles={resizeEnabled ? ['se'] : []}
                handleStyles={{
                se: { cursor: 'nwse-resize', backgroundColor: resizeColor },
            }}
              >
                <div
                  style={{
                    width: imageSize.width,
                    height: imageSize.height,
                    position: 'relative',
                    border: `${resizeColor}`,
                  }}
                  onContextMenu={handleRightClick} //      
                  ref={imageRef} //    
                >
                  <img
                    src={imageSrc}
                    alt="Selected"
                    style={{
                      width: '100%',
                      height: '100%',
                      objectFit: 'cover',
                    }}
                  />
                </div>
              </Resizable>
            </Draggable>
          )}
      </div>
      {/*   */}
      {showDeletePopup  && (
          <div
            className="popup"
            style={{
              position: 'absolute',
              top: `${popupPosition.y + 200}px`,
              left: `${popupPosition.x + 490}px`,
              backgroundColor: 'gray',
              radius : '20px',
              padding: '10px',
              zIndex: 1000,
            }}
          >
            <button onClick={handleDelete}></button>
          </div>
        )}
      <div className="footer">
        <button className="complete-button"onClick={saveBookCoverAsImage}></button>
      </div>
    </div>
  );
}

export default BookDesignPage;