import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './HomePage.css';
import bookImage from '../assets/images/book3.png';

function HomePage() {
  const navigate = useNavigate();
  const [id, setId] = useState('');
  const [pw, setPw] = useState('');

  const handleLogin = async (event) => {
    event.preventDefault();

    // If either id or pw is empty, navigate directly to /main
    if (!id || !pw) {
      navigate('/my-autobiography');
      return;
    }

    try {
      const response = await fetch('/auth/login_process', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ id, pw }),
        credentials: 'include'
      });

      const data = await response.json();
      if (data.success) {
        navigate('/main');
      } else {
        alert(data.message);
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const handleSignupClick = () => {
    navigate('/signup');
  };

  return (
    <div className="login-container">
      <div className="login-box">
        <div className="book-image-container">
          <img src={bookImage} alt="Book" className="book-image" />
        </div>
        <div className="login-content">
          <h1 className="title"></h1>
          <form onSubmit={handleLogin} className="login-form">
            <input
              type="text"
              placeholder=""
              className="input-field"
              value={id}
              onChange={(e) => setId(e.target.value)}
            />
            <input
              type="password"
              placeholder=""
              className="input-field"
              value={pw}
              onChange={(e) => setPw(e.target.value)}
            />
            <button type="submit" className="login-button"></button>
          </form>
          <div className="links">
            <span> </span> | <span> </span> | <span onClick={handleSignupClick}></span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomePage;