import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './ModifyInfo.css';
import signupIcon from '../assets/images/signup-icon.png';
import defaultProfileImage from '../assets/images/signup-icon.png';
import search from '../assets/images/search.png';
import exit from '../assets/images/x.png';
function SignupPage() {
  
const [profileImage, setProfileImage] = useState(null);
  const [formData, setFormData] = useState({
    id: '',
    pw: '',
    pw2: '',
    username: '',
    email: '',
    phone_num: '',
    birth: '',
    gender: ''
  });
  const [passwordError, setPasswordError] = useState(false);
  const navigate = useNavigate();
  const [userName, setUserName] = useState(''); //     
  const [profileImagePath, setProfileImagePath] = useState(defaultProfileImage); //     
  const [isSidebarVisible, setIsSidebarVisible] = useState(false);

  useEffect(() => {
    fetch('/api/get_user_info')
      .then(response => response.json())
      .then(data => {
        if (data.success) {
          setUserName(data.nickname); //  
          setProfileImagePath(data.imagePath || defaultProfileImage); //    
  
          //   formData 
          setFormData({
            id: data.id || '',  //    ID
            pw: '', //     
            pw2: '', //    
            username: data.nickname || '', //  
            email: data.email || '', //  
            phone_num: data.phone_number || '', //  
            birth: data.birth || '', //  
            gender: data.gender || '' //  
          });
        } else {
          console.error(data.message);
          navigate('/');
        }
      })
      .catch(error => {
        console.error('Error fetching user info:', error);
      });
  }, [navigate]);
  
  
  const handleMenuClick = () => {
    setIsSidebarVisible(true);
  };

  const handleExitClick = () => {
    setIsSidebarVisible(false);
  };
  const handleBookClick = () => {
    navigate('/my-autobiography');
  };
  const handleInquiryClick = () => {
    navigate('/inquiry');
  };
  const handleHomeClick = () => {
    navigate('/');
  };

  const handleProfileImageChange = (event) => {
    const file = event.target.files[0];
    setProfileImage(file); //    
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value
    });

    if (name === 'pw') {
      validatePassword(value);
    }
  };

  const validatePassword = (password) => {
    const passwordRegex = /^(?=.*[A-Z])(?=.*\d).{8,}$/;
    setPasswordError(!passwordRegex.test(password));
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    if (passwordError) {
      alert('  .');
      return;
    }

    const data = new FormData();
    for (const key in formData) {
      data.append(key, formData[key]);
    }
    if (profileImage) {
      data.append('profileImage', profileImage); //   
    }

    fetch('/auth/register_process', {
      method: 'POST',
      body: data
    })
    .then(response => response.json())
    .then(data => {
      if (data.success) {
        alert(data.message);
        navigate('/');
      } else {
        alert(data.message);
      }
    })
    .catch((error) => {
      console.error('Error:', error);
    });
  };

  const handleLogout = async () => {
    try {
      const response = await fetch('/api/logout', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
      });
  
      if (response.ok) {
        navigate('/');  //      
      } else {
        console.error('Failed to log out');
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };


  return (
    <div className="signup-container">
      <label htmlFor="profile-image-input">
        <img src={profileImage ? URL.createObjectURL(profileImage) : profileImagePath} alt="Signup Icon" className="signup-icon" />
      </label>
      <input
        type="file"
        id="profile-image-input"
        accept="image/*"
        style={{ display: 'none' }}
        onChange={handleProfileImageChange}
      />
      <form className="signup-form" onSubmit={handleSubmit}>
        <div className="input-group">
        <input 
          type="text" 
          placeholder=""
          name="id" 
          value={formData.id}
          onChange={handleChange}
          className="input-field" 
          disabled //   
        />
        </div>
        <input 
          type="password" 
          placeholder="" 
          name="pw"
          value={formData.pw}
          onChange={handleChange}
          className={`input-field ${passwordError ? 'error' : ''}`} 
        />
        <span className="password-hint">*,    8 </span>
        {passwordError && <span className="error-message">  .</span>}
        <input 
          type="password" 
          placeholder=" " 
          name="pw2"
          value={formData.pw2}
          onChange={handleChange}
          className="input-field" 
        />
        <input 
          type="text" 
          placeholder="" 
          name="username"
          value={formData.username}
          onChange={handleChange}
          className="input-field" 
        />
        <div className="gender-container">
          <label>
            <input 
              type="radio" 
              name="gender" 
              value=""
              checked={formData.gender === ''}
              onChange={handleChange} 
            />
            
          </label>
          <label>
            <input 
              type="radio" 
              name="gender" 
              value=""
              checked={formData.gender === ''}
              onChange={handleChange} 
            />
            
          </label>
        </div>
        <input 
          type="date" 
          placeholder="" 
          name="birth"
          value={formData.birth}
          onChange={handleChange}
          className="input-field" 
        />
        <input 
          type="text" 
          placeholder=" " 
          name="phone_num"
          value={formData.phone_num} //   
          onChange={handleChange}
          className="input-field" 
        />
        <input 
          type="email" 
          placeholder="" 
          name="email"
          value={formData.email}
          onChange={handleChange}
          className="input-field" 
        />
        <button type="submit" className="submit-button"></button>
      </form>
    </div>
  );
}

export default SignupPage;
