import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom'; // useNavigate 
import './chatbot.css';
import chatbotImage from '../assets/images/chatbot1.png';
import back from '../assets/images/exit.png';


function Chatbot({ onClose }) {
  const [messages, setMessages] = useState([
    { type: 'bot', text: '!  .      .     !' },
  ]);

  const [inputValue, setInputValue] = useState('');
  const { bookId } = useParams();  // URL  bookId 
  const [isLoading, setIsLoading] = useState(false); //   
  const [error, setError] = useState(null); //   
  const [isConversationEnded, setIsConversationEnded] = useState(false); //    
  const navigate = useNavigate(); //    hook

  useEffect(() => {
    const fetchUserInfo = async () => {
      try {
        const response = await fetch('/api/get_user_info'); //    
        if (!response.ok) {
          throw new Error('     .');
        }
        const data = await response.json();
        if (data.success) {
          return data.id; //  ID 
        } else {
          throw new Error('    .');
        }
      } catch (error) {
        console.error('Error fetching user info:', error);
        return null; //   null 
      }
    };
  
    const fetchInitialInput = async (userId) => {
      try {
        const response = await fetch(`/api/get-initial-input/${bookId}/${userId}`);
        if (!response.ok) {
          throw new Error('     .');
        }
        const data = await response.json();
        return data.content;  //    content 
      } catch (error) {
        console.error('Error fetching initial input:', error);
        return '';  //     
      }
    };
  
    const initiateChat = async () => {
      setIsLoading(true); //   
      try {
        const userId = await fetchUserInfo(); //  ID 
        if (!userId) {
          throw new Error('     .');
        }
        const initialInputContent = await fetchInitialInput(userId); //    
        const response = await fetch(`/api/chatbot/initiate/${bookId}`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ initialInput: initialInputContent }),
        });
        if (!response.ok) {
          throw new Error('      .');
        }
        const data = await response.json();
        setMessages(prevMessages => [
          ...prevMessages,
          { type: 'bot', text: data.question }
        ]);
      } catch (error) {
        console.error('Error:', error);
        setMessages(prevMessages => [
          ...prevMessages,
          { type: 'bot', text: '     .' }
        ]);
      } finally {
        setIsLoading(false); //   
      }
    };
  
    initiateChat();
  }, [bookId]);

  //      
  const handleSend = async () => {
    if (inputValue.trim()) {
      const previousQuestion = messages[messages.length - 1].text;
      setMessages([...messages, { type: 'user', text: inputValue }]);
      setIsLoading(true); //   

      try {
        const response = await fetch(`/api/chatbot/ask/${bookId}`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ userInput: inputValue, previousQuestion }),
        });
        if (!response.ok) {
          throw new Error('     .');
        }

        const data = await response.json();

        //        
        if (data.message === ' . !') {
          setMessages(prevMessages => [
            ...prevMessages,
            { type: 'bot', text: data.message }
          ]);

          //   
          setIsConversationEnded(true);

          //   API    
          try {
            //  content  API 
            const summaryResponse = await fetch(`/api/chatbot/summary`, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                bookId: bookId,
                inputCount: 1
              }),
            });
            if (!summaryResponse.ok) {
              throw new Error('     .');
            }

            const summaryData = await summaryResponse.json();

            //    book_reading API 
            const writeResponse = await fetch('/api/write_process/book_reading', {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                content: summaryData.content, //  content 
                bookId: data.bookId, //   bookId 
              }),
            });
            if (!writeResponse.ok) {
              throw new Error('book     .');
            }

            //    
            navigate(`/book-reading/${bookId}`);
          } catch (error) {
            console.error('Error:', error);
            setError('book_reading     .');
          }

          return;  //      
        }

        //    
        setMessages(prevMessages => [
          ...prevMessages,
          { type: 'bot', text: data.question }
        ]);

      } catch (error) {
        console.error('Error:', error);
        setError('    .  .');
        setMessages(prevMessages => [
          ...prevMessages,
          { type: 'bot', text: '    .' }
        ]);
      } finally {
        setIsLoading(false); //   
      }
    }
  };
  
  //    ' '  '  '  
  const handleCreateBook = () => {
    navigate(`/book-reading/${bookId}`); //   
  };

  const handleAddContent = () => {
    navigate(`/main/${bookId}`); //     
    };

  // Enter       
  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSend();
    }
  };

  return (
    <div className="chatbot-container">
      <div className="chatbot-header">
        <img src={back} alt="back" className="back-icon" onClick={onClose} />
        <div className="progress-bar"></div>
      </div>
      <div className="chatbot-body">
        {messages.map((msg, index) => (
           <div key={index} className={`message-container ${msg.type}`}>
            {msg.type === 'bot' && (
              <img src={chatbotImage} alt="Chatbot" className="chatbot-profile" />
            )}
            <div className={`message ${msg.type}`}>
              {msg.text}
            </div>
         </div>
        ))}
        {isLoading && <div className="loading-indicator"> ...</div>}
        {error && <div className="error-message">{error}</div>}
      </div>
      {/*      */}
      {isConversationEnded && (
        <div className="conversation-end-buttons">
          <button onClick={handleCreateBook}>    </button>
          <button onClick={handleAddContent}>   </button>
        </div>
      )}
      {!isConversationEnded && (
        <div className="chatbot-footer">
          <input
            type="text"
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            onKeyPress={handleKeyPress} // Enter  
            placeholder="  ..."
            disabled={isLoading} //    
          />
          <button onClick={handleSend} disabled={isLoading || !inputValue.trim()}>
            
          </button>
        </div>
      )}
    </div>
  );
}

export default Chatbot;
